.form-container {
  grid-template-rows: auto auto auto;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  display: grid;
}

.form-input {
  grid-template-rows: subgrid;
  grid-row: span 3;
  gap: 0;
  display: grid;
}

.form-input > label {
  align-self: end;
}

.form-input.is-col-span-1 {
  grid-column: span 1;
}

.form-input.is-col-span-2 {
  grid-column: span 2;
}

.is-disabled, .is-disabled > .label {
  color: #dbdbdb;
}

@media (width <= 768px) {
  .form-container {
    display: block;
  }

  .form-input {
    padding: .5rem 0;
  }
}
